import { cost, Boat_Make, Boat } from "../Common.fs.js";
import { copy, fill, equalsWith } from "../fable_modules/fable-library.4.5.0/Array.js";
import { comparePrimitives, defaultOf, equals } from "../fable_modules/fable-library.4.5.0/Util.js";
import { nonSeeded } from "../fable_modules/fable-library.4.5.0/Random.js";
import { min } from "../fable_modules/fable-library.4.5.0/Double.js";
import { ofArray } from "../fable_modules/fable-library.4.5.0/List.js";
import { initialize, minBy } from "../fable_modules/fable-library.4.5.0/Seq.js";

export function arrangeRandom(size, paddlers) {
    let testExpr;
    if (size <= 0) {
        return new Boat(0, new Array(0), new Array(0));
    }
    else if ((testExpr = paddlers, !equalsWith(equals, testExpr, defaultOf()) && (testExpr.length === 0))) {
        const size_3 = size | 0;
        return new Boat(size_3, fill(new Array(size_3), 0, size_3, void 0), fill(new Array(size_3), 0, size_3, void 0));
    }
    else {
        const rand = nonSeeded();
        const tempArray = copy(paddlers);
        const a = tempArray;
        let n = a.length;
        while (1 < n) {
            const k = rand.Next1(n) | 0;
            n = ((n - 1) | 0);
            const temp = a[n];
            const value = a[k];
            a[n] = value;
            a[k] = temp;
        }
        const leftSize = min(size, ~~((1 + tempArray.length) / 2)) | 0;
        return Boat_Make(size, ofArray(tempArray.slice(void 0, (leftSize - 1) + 1)), ofArray(tempArray.slice(leftSize, ((leftSize + size) - 1) + 1)));
    }
}

export function searchRandom(attempts, size, paddlers) {
    return minBy(cost, initialize(attempts, (_arg) => arrangeRandom(size, paddlers)), {
        Compare: comparePrimitives,
    });
}

